/*  start variable */
:root{
  --Dark-Blue: hsl(217, 28%, 15%);/* (intro and email sign up background) */
  --Dark-Blue : hsl(218, 28%, 13%);/*(main background)*/
  --Dark-Blue : hsl(216, 53%, 9%);/*(footer background)*/
  --Dark-Blue : hsl(219, 30%, 18%);/*(testimonials background) */
  --Cyan : hsl(176, 68%, 64%);/*(inside call-to-action gradient)*/
  --Blue : hsl(198, 60%, 50%);/*(inside call-to-action gradient)*/
  --Light-Red : hsl(0, 100%, 63%);/*(error)*/
}
/* end variable */
 /* start component  */
 .special-heading{
  color: #ecebed;
  font-weight: 800;
  font-size: 100px;
  letter-spacing: -3px;
  margin: 0;
  text-align: center;
 }
 .special-heading + p{
  margin: -40px 0;
  text-align: center;
  font-size: 20px;
  color: #777;
 }
 @media (max-width:760px) {
  .special-heading{
    font-size: 60px;
  
   }
   .special-heading + p{
margin-top: -25px;
    
   }
 }
  /* end componnent  */
/* Start Global Rules */
* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  html {
    scroll-behavior: smooth;
  }
  body {
    font-family: 'Kumbh Sans', sans-serif !important;
    font-family: 'Manrope', sans-serif;
    font-family: 'Open Sans', sans-serif;
    font-family: 'Raleway', sans-serif;
    font-family: 'Space Mono', monospace;  }
  .container {
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
  }
  /* Small */
  @media (min-width: 768px) {
    .container {
      width: 750px;
    }
  }
  /* Medium */
  @media (min-width: 992px) {
    .container {
      width: 970px;
    }
  }
  /* Large */
  @media (min-width: 1200px) {
    .container {
      width: 1170px;
    }
  }
  /* End Global Rules */

  /* start header section  */
  header{
    background-color: var(--Dark-Blue);
    padding: 15px;
  }
header .container{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .container img{
  width: 80px;
}
header .container nav ul{
  display: flex;
  list-style: none;
}
header .container nav ul li {
  margin: 0 10px;
}
header .container nav ul li a{
color: #777;
text-decoration: none;
font-family: 'Kumbh Sans', sans-serif;
font-family: 'Manrope', sans-serif;
font-family: 'Raleway', sans-serif;
font-family: 'Space Mono', monospace;
}
header .container nav ul li a:hover{
  color: white;
  border-bottom: 2px solid white;
}
  /* end header section  */
  /* start landing section  */
.landing{

  background-color: var(--Dark-Blue);
  padding-bottom: 150px;
}
.landing .container{   
  position: relative;
  z-index: 1;
}
.landing .container .img-landing {
  text-align: center;
  margin: auto;
}
.landing .container .img-landing  img{
  width: 500px;
  max-width: 100%;
}
.landing .container .img-landing h1{
  width: 450px;
  max-width: 100%;
  color: white;
  margin: auto;
  margin-top: 25px;
  font-family: 'Kumbh Sans', sans-serif;
font-family: 'Manrope', sans-serif;
font-family: 'Raleway', sans-serif;
font-family: 'Space Mono', monospace;
}
.landing .container .description {
margin: auto;
text-align: center;
}
.landing .container .description p{
  width: 450px;
  max-width: 100%;
  color: #777;
  margin: auto;
  font-size: 18px;
  line-height: 1.5;
  margin-top: 5px;
}
.landing .container .description button{
  /* display: block; */
  border: none;
  padding: 10px 30px;
  background: var(--Cyan);
  color: white;
  cursor: pointer;
  margin-top: 17px;
  outline: none;

  /* margin-bottom: 12px; */
  border-radius: 10px;
  font-family: 'Kumbh Sans', sans-serif;
  font-family: 'Manrope', sans-serif;
  font-family: 'Raleway', sans-serif;
  font-family: 'Space Mono', monospace;
    transition: all 0.3s ease-in-out;

    box-shadow: 0 0 10px  hsla(176, 68%, 64%, 0);; 
}.landing .container .description button:hover{
 
    box-shadow: 0 0 20px  hsl(176, 68%, 64%);;
}
.landing> img{
  position: absolute;
  top: 552px;
  width: 100%;
 bottom: 0;
}
@media (max-width:767px){
  .landing{
    padding-bottom: 60px;
  }
  .landing> img{
    position: absolute;
    top: 742px;
    width: 100%;
   bottom: 0;
  }
  header .container nav ul li {
    margin: 0 6px;
  }
}
  /* endd landing section  */
  /* start adventure senction */
.adventure{
  background-color:  hsl(217.89deg 28.36% 13.14%);
  padding-top: 30px;
  padding-bottom: 30px;
}
.adventure .container{
  margin: auto;
  width: 800px;
  max-width: 100%;
}
.adventure .container .card-adventure{
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(300px, 1fr));
  column-gap: 79px;
  row-gap: 20px;
}
.adventure .container .card-adventure .card{
 text-align: center;
}
.adventure .container .card-adventure .card .content h3{
  color: white;
}
.adventure .container .card-adventure .card .content p{
  color: #777;
  line-height: 1.2;
}

  /* q end adventure  section*/

  /*  start productive section */
.prodective{
  background-color:  hsl(217.89deg 28.36% 13.14%);
  padding-top: 30px;
  padding-bottom: 30px;
}
.prodective  .container .content{
  display:flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}
.prodective  .container .content .img-prod{
  width: 500px;
  max-width: 100%;
}
.prodective  .container .content .img-prod img{
  width: 100%;
}
.prodective  .container .content .text-prod{
  width: 450px;
  max-width: 100%;
}
.prodective  .container .content .text-prod h1{
color: white;
width: 290px;
line-height: 1.5;
font-size: 35px;
}
.prodective  .container .content .text-prod p{
  color: #777;
  line-height: 1.5;
margin-bottom: 20px;
}
.prodective  .container .content .text-prod span{
  color:var(--Cyan);
  line-height: 1.5;
  position: relative;

}
.prodective  .container .content .text-prod span img{
width: 30px;
line-height: 1.5;

}
.prodective  .container .content .text-prod span:before{
  content:"";
  position: absolute;
  bottom: -3px;
  height: 2px;
  left: 0;
  width: 0;
  background-color: var(--Cyan);
  transition: all 0.3s;

}
.prodective  .container .content .text-prod span:hover::before{
  width: 100%;
  
}
  /* end productive section */

  /* sttart testimonial section  */
.testim{
  background-color:  hsl(217.89deg 28.36% 13.14%);
  padding-top: 30px;
  padding-bottom: 140px;
}

.testim .container img{
  margin-left: 15px;
  margin-bottom: -10px;
}
.testim .container .testimonial{
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(300px, 1fr));
  grid-gap: 30px;
}
.testim .container .testimonial .feed{
 background-color: var(--Dark-Blue);
 padding: 15px;
 border-radius: 5px;
}
.testim .container .testimonial .feed >p{
  color: #777;
}
.testim .container .testimonial .feed .people{
  display: flex;
}
.testim .container .testimonial .feed .people img{
  border-radius: 50%;
  width: 70px;
  height: 70px;
  margin-right: 20px;
}
.testim .container .testimonial .feed .desc {
margin-top: -16px;
}
.testim .container .testimonial .feed .desc h2{
 color: white;
}
.testim .container .testimonial .feed .desc p{
 color: #777;
}
  /* end testtimonial section  */

  /* start vontact box/ */
.box-contact{
  background-color:  hsl(217.89deg 28.36% 13.14%);
  padding-top: 90px;
  /* padding-bottom: 30px; */
  position: relative;

}
.box-contact .container .content-box{
  width: 800px;
  max-width: 100%;
  margin: auto;
  background-color: var(--Dark-Blue);
  padding: 15px;
  position: absolute;
  top: -45px;
  left: 50%;
  transform: translateX(-50%);
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
}
.box-contact .container .content-box h1{
color: white;
text-align: center;
margin-bottom: 10px;
}
.box-contact .container .content-box p{
  color: white;
text-align: center;
margin-bottom: 10px;
line-height: 1.5;
}
.box-contact .container .content-box form{
  margin-bottom: 20px;
}
.box-contact .container .content-box form input{
  width: 500px;
  max-width: 100%;
  padding: 10px;
  border-radius: 15px;
  border:none;
}
.box-contact .container .content-box form button{
  padding: 10px;
  border-radius: 15px;
  border:none;
  cursor: pointer;
  outline: none;
  transition: all .3s ease;
  color: white;
  background-color: var(--Cyan);
  margin-left: 20px;
  font-family: 'Kumbh Sans', sans-serif;
font-family: 'Manrope', sans-serif;
font-family: 'Raleway', sans-serif;
font-family: 'Space Mono', monospace;
}
@media (max-width:667px) {
  .box-contact .container .content-box form button{
    margin-top: 10px;
    margin-left: 0 !important;
    width: 100%;
    display: block;
  }
  .footer{
    padding-top: 0px;
  }
}
  /* end contact box */

  /*  start  footer section */ 
.footer{
  background-color:  var(--Dark-Blue);
  padding-top: 190px;
  padding-bottom: 30px;
}
.footer .container .links{
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(200px, 1fr));
  grid-gap: 30px;
  margin-top: 20px;
}
.footer .container .links .location{
  display: flex;
}
.footer .container .links .location img{
width: 30px;
height: 30px;
margin-right:10px ;
}
.footer .container .links .location p{
color: #777;
line-height: 1.4;
margin-top: -5px;
}
.footer .container .links .phonemail .phone {
  margin-bottom: 30px;
}
.footer .container .links .phonemail .phone,
.footer .container .links .phonemail .email{
  display: flex;
}
.footer .container .links .phonemail .phone img,
.footer .container .links .phonemail .email img{
  width: 30px;
  height: 30px;
  margin-right:10px ;
}
.footer .container .links .phonemail .phone p,
.footer .container .links .phonemail .email p{
  color: #777;
  line-height: 1.4;
  margin-top: -2px;
}
.footer .container .links .aboutus{
margin-top: -20px;
}

.footer .container .links .aboutus ul{
  list-style: none;
}
.footer .container .links .aboutus ul li{
padding-bottom: 10px;
}
.footer .container .links .aboutus ul li a,
.footer .container .links .aboutus ul li:first-child{
  text-decoration: none;
  color: #777;
}
.footer .container .links .aboutus ul li a:hover{
  color: white;
}
.footer .container .links .contactus{
  margin-top: -20px;
}
.footer .container .links .contactus ul{
  list-style: none;
}
.footer .container .links .contactus ul li{
padding-bottom: 14px;
}
.footer .container .links .contactus ul li a,
.footer .container .links .contactus ul li:first-child{
  text-decoration: none;
  color: #777;
}
.footer .container .links .contactus ul li a:hover{
  color: white;
}
.footer .container .links .social-icon{
text-align: center;
}
.footer .container .links .social-icon i{
  color: var(--Cyan);
  border:  2px solid  var(--Cyan);
  padding: 4px;
  border-radius: 50%;
  font-size: 18px;
  margin: 0 10px;
}
  /*  start footer section */